.JokeList {
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  height: 80%;
}

.JokeList-side {
  background: #4895ef;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 30%;
  text-align: center;
  -webkit-box-shadow: 5px 5px 5px 0px #797272, inset 4px 4px 15px 0px #797272, 5px 5px 15px 5px rgba(23,131,255,0);
          box-shadow: 5px 5px 5px 0px #797272, inset 4px 4px 15px 0px #797272, 5px 5px 15px 5px rgba(23,131,255,0);
  z-index: 2;
}
.JokeList-side img {
  width: 50%;
}

.JokeList-title {
  font-size: 4vw;
  margin: 2rem;
  color: white;
  font-weight: 300;
  letter-spacing: 0.5rem;
}

.JokeList-title span {
  font-weight: bold;
}

.JokeList-jokes {
  height: 90%;
  background: white;
  -ms-flex-item-align: center;
      align-self: center;
  width: 70%;
  overflow: scroll;
  -webkit-box-shadow: 5px 5px 5px 0px #bbb4b4, inset 4px 4px 15px 0px #bbb4b4, 5px 5px 15px 5px rgba(23,131,255,0);
          box-shadow: 5px 5px 5px 0px #bbb4b4, inset 4px 4px 15px 0px #bbb4b4, 5px 5px 15px 5px rgba(23,131,255,0);
}

@media only screen and (max-width: 600px) {
  .JokeList-side {
    width: 40%;
  }
}


